___CSS_LOADER_EXPORT___.push([module.id, "\n.deploy-setting-container[data-v-07ef36d1] {\n    padding: 5px;\n}\n#product-input[data-v-07ef36d1] {\n    width: 50%;\n}\n#button-container[data-v-07ef36d1] {\n    text-align: end;\n    vertical-align: bottom;\n}\n#previous-button[data-v-07ef36d1] {\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #2f3160;\n    opacity: 1;\n    font-weight: bold;\n}\n.default-button[data-v-07ef36d1] {\n    background: #2f3160 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n    margin-left: 10px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/deploySettings.vue"],"names":[],"mappings":";AAkhBA;IACI,YAAY;AAChB;AACA;IACI,UAAU;AACd;AACA;IACI,eAAe;IACf,sBAAsB;AAC1B;AACA;IACI;yCACqC;IACrC,kDAAkD;IAClD,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;IACV,iBAAiB;AACrB;AACA;IACI,+CAA+C;IAC/C,kBAAkB;IAClB,UAAU;IACV;yCACqC;IACrC,kDAAkD;IAClD,mBAAmB;IACnB,2CAA2C;IAC3C,mBAAmB;IACnB,cAAc;IACd,UAAU;IACV,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { onBeforeMount, ref } from \"vue\";\nimport { container } from \"tsyringe\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { IModule, IModuleSettingsItem, IApplicationSettingsItem, IModuleDeployParameters } from \"../../../../domain/model/models\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { IGitProject } from \"../../../../domain/infrastructure/IProviderApi\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IDebounce } from \"../../../../domain/infrastructure/IDebounce\";\nimport { IApplicationSettings } from \"../../../../domain/useCase/IApplicationSettings\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\n\nonBeforeMount(() => {\n    fillDatatableRow();\n    getGitGroupsFromProvider();\n    getProductsFromProvider();\n});\n\ninterface IProduct {\n    products: Array<IGitProject>;\n    loaded: boolean;\n}\n\ninterface IGroup {\n    value: IGitProject;\n    microservicesCache: Array<IGitProject>;\n    requestedBefore: boolean;\n}\n\ninterface IGitGroup {\n    groups: Array<IGroup>;\n    loaded: boolean;\n}\n\ninterface IModuleDataTableRow {\n    moduleID: IModule[\"ID\"];\n    moduleName: IModule[\"name\"];\n    gitGroupName: string;\n    gitGroupID: number;\n    microservices: Array<IGitProject>;\n    microservicesDisable: boolean;\n    microserviceName: string;\n    microserviceID: number;\n}\n\ninterface IAppDataTableRow {\n    appID: IModule[\"ID\"];\n    appName: IModule[\"name\"];\n    microservices: Array<IGitProject>;\n    microservicesDisable: boolean;\n}\n\nconst emit = defineEmits([\"close\"]);\n\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst notification = container.resolve<INotification>(INotification);\nconst appSettingsUseCase = container.resolve<IApplicationSettings>(IApplicationSettings);\nconst debouncer = container.resolve<IDebounce>(IDebounce);\n\nconst gitProducts = ref({ products: [], loaded: false } as IProduct);\nconst gitGroups = ref({ groups: [], loaded: false } as IGitGroup);\nconst currentApp = ref(viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\nconst appSettings = ref({ product: { ID: 0, name: \"\" }, gitGroup: { ID: 0, name: \"\" }, microservice: { ID: 0, name: \"\" } });\nconst modulesDataTableRows = ref(Array<IModuleDataTableRow>());\nconst appDataTableRows = ref(Array<IAppDataTableRow>());\n\nconst modulesAppDatatableColumns = ref([\"moduleName\", \"gitGroup\", \"microservice\"]);\nconst appAppDatatableColumns = ref([\"appName\", \"gitGroup\", \"microservice\"]);\nconst modulesOptionsDataTable = ref({\n    filterable: false,\n    perPage: 5,\n    perPageValues: [],\n    headings: {\n        moduleName: localization.get(\"moduleName\"),\n        gitGroup: localization.get(\"gitGroup\"),\n        microservice: localization.get(\"microservice\"),\n    },\n    sortable: [],\n    texts: {\n        count: \"\",\n    },\n});\n\nconst appOptionsDataTable = ref({\n    filterable: false,\n    perPage: 5,\n    perPageValues: [],\n    headings: {\n        appName: localization.get(\"appName\"),\n        gitGroup: localization.get(\"gitGroup\"),\n        microservice: localization.get(\"microservice\"),\n    },\n    sortable: [],\n    texts: {\n        count: \"\",\n    },\n});\n\nfunction fillDatatableRow() {\n    modulesDataTableRows.value = viewModel.studio.items\n        .filter((item) => item.objectType === \"module\")\n        .map<IModuleDataTableRow>((item) => {\n            const module: IModuleDataTableRow = {\n                moduleID: item.ID,\n                moduleName: item.name,\n                gitGroupName: \"\",\n                gitGroupID: (item as IModule).deployParameters?.gitGroupId || 0,\n                microservices: [],\n                microservicesDisable: true,\n                microserviceName: \"\",\n                microserviceID: (item as IModule).deployParameters?.gitServiceRepoId || 0,\n            };\n            return module;\n        });\n    appDataTableRows.value.push({\n        appID: currentApp.value!.ID,\n        appName: currentApp.value!.name,\n        microservices: [],\n        microservicesDisable: true,\n    });\n}\n\nfunction getProductsFromProvider() {\n    appSettingsUseCase\n        .getProjectsOfProduct()\n        .then((data) => {\n            gitProducts.value.products = data;\n            gitProducts.value.loaded = true;\n            setSelectedProduct();\n        })\n        .catch(() => {\n            notification.showNotification({ text: \"Error while fetching products\", type: \"error\" });\n        });\n}\n\nfunction setSelectedProduct() {\n    if (currentApp.value) {\n        if (currentApp.value.deployParameters) {\n            const currentProduct = gitProducts.value.products.find((product) => currentApp.value!.deployParameters!.gitProductRepoId == product.id);\n            if (currentProduct) {\n                appSettings.value.product = {\n                    name: currentProduct.name,\n                    ID: currentProduct.id,\n                };\n            }\n        }\n    }\n}\n\nasync function getGitGroupsFromProvider() {\n    appSettingsUseCase\n        .getGroupNamesWithId()\n        .then((data) => {\n            data.forEach((fetchedGroup) =>\n                gitGroups.value.groups.push({\n                    value: fetchedGroup,\n                    microservicesCache: [],\n                    requestedBefore: false,\n                })\n            );\n            gitGroups.value.loaded = true;\n            setModulesSelectedGitGroups();\n            setAppSelectedGitGroup();\n        })\n        .catch(() =>\n            notification.showNotification({\n                text: \"Error while fetching git groups\",\n                type: \"error\",\n            })\n        );\n}\n\nfunction setModulesSelectedGitGroups() {\n    modulesDataTableRows.value.forEach((row) => {\n        if (row.gitGroupID !== 0) {\n            const group = gitGroups.value.groups.find((group) => group.value.id == row.gitGroupID);\n            if (group) {\n                row.gitGroupName = group.value.name;\n            } else {\n                row.gitGroupID = 0;\n            }\n        }\n    });\n    setModulesSelectedMicroservices();\n}\n\nfunction setAppSelectedGitGroup() {\n    if (currentApp.value) {\n        if (currentApp.value.deployParameters) {\n            const currentGitGroup = gitGroups.value.groups.find((group) => currentApp.value!.deployParameters!.gitGroupId == group.value.id);\n            if (currentGitGroup) {\n                appSettings.value.gitGroup = {\n                    name: currentGitGroup.value.name,\n                    ID: currentGitGroup.value.id,\n                };\n            }\n        }\n    }\n\n    if (appSettings.value.gitGroup.ID !== 0) {\n        const group = gitGroups.value.groups.find((group) => group.value.id == appSettings.value.gitGroup.ID);\n        if (group) {\n            appSettings.value.gitGroup.name = group.value.name;\n        } else {\n            appSettings.value.gitGroup.ID = 0;\n        }\n    }\n    setAppSelectedMicroservice();\n}\n\nfunction setModulesSelectedMicroservices() {\n    const groupIdList: Array<number> = modulesDataTableRows.value\n        .map((row) => row.gitGroupID)\n        .filter((id, index, self) => id !== 0 && self.indexOf(id) === index);\n\n    groupIdList.forEach((id) => {\n        appSettingsUseCase\n            .getProjectsByGroupId(id)\n            .then((data) => {\n                const group = gitGroups.value.groups.find((group) => group.value.id == id);\n                if (group) {\n                    group.microservicesCache = data;\n                    group.requestedBefore = true;\n                    modulesDataTableRows.value.forEach((row) => {\n                        if (row.gitGroupID === id) {\n                            row.microservices = data;\n                            if (row.microserviceID !== 0) {\n                                const microservice = data.find((service) => service.id == row.microserviceID);\n                                if (microservice) {\n                                    row.microserviceName = microservice.name;\n                                    row.microservicesDisable = false;\n                                } else {\n                                    row.microserviceID = 0;\n                                    row.microservicesDisable = true;\n                                }\n                            }\n                        }\n                    });\n                }\n            })\n            .catch(() =>\n                notification.showNotification({\n                    text: \"Error while fetching projects\",\n                    type: \"error\",\n                })\n            );\n    });\n}\n\nfunction setAppSelectedMicroservice() {\n    if (appSettings.value.gitGroup.ID != 0) {\n        appSettingsUseCase\n            .getProjectsByGroupId(appSettings.value.gitGroup.ID)\n            .then((data) => {\n                const group = gitGroups.value.groups.find((group) => group.value.id == appSettings.value.gitGroup.ID);\n                if (group) {\n                    group.microservicesCache = data;\n                    group.requestedBefore = true;\n                    appDataTableRows.value[0].microservices = data;\n                    if (currentApp.value) {\n                        if (currentApp.value.deployParameters) {\n                            const microservice = data.find((service) => service.id == currentApp.value?.deployParameters?.gitServiceRepoId);\n                            if (microservice) {\n                                appSettings.value.microservice.name = microservice.name;\n                                appDataTableRows.value[0].microservicesDisable = false;\n                            } else {\n                                appSettings.value.microservice.ID = 0;\n                                appDataTableRows.value[0].microservicesDisable = true;\n                            }\n                        }\n                    }\n                }\n            })\n            .catch(() =>\n                notification.showNotification({\n                    text: \"Error while fetching projects\",\n                    type: \"error\",\n                })\n            );\n    }\n}\n\nfunction handleProductSelect() {\n    const product = gitProducts.value.products.find((product) => product.name === appSettings.value.product.name);\n    if (product) {\n        appSettings.value.product.ID = product.id;\n    } else {\n        appSettings.value.product.name = \"\";\n        appSettings.value.product.ID = 0;\n    }\n}\n\nasync function handleModulesGitGroupInput(selectedRow: IModuleDataTableRow) {\n    debouncer.resetTimer();\n    const selectedGitGroup = gitGroups.value.groups.find((gitGroup) => gitGroup.value.name === selectedRow.gitGroupName);\n    const currentRow = modulesDataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.microservicesDisable = true;\n    currentRow.microservices = [];\n    currentRow.microserviceName = \"\";\n    currentRow.microserviceID = 0;\n\n    if (selectedGitGroup) {\n        currentRow.gitGroupID = selectedGitGroup.value.id;\n        currentRow.gitGroupName = selectedGitGroup.value.name;\n\n        if (!selectedGitGroup.requestedBefore) {\n            debouncer.debounce(getMicroservicesByGroupID, 500, selectedGitGroup, currentRow);\n        } else {\n            currentRow.microservices = selectedGitGroup.microservicesCache;\n            currentRow.microservicesDisable = !currentRow.microservices.length;\n        }\n    } else {\n        currentRow.gitGroupName = selectedRow.gitGroupName;\n        currentRow.gitGroupID = 0;\n    }\n}\n\nasync function handleAppGitGroupInput(selectedRow: IAppDataTableRow) {\n    debouncer.resetTimer();\n    const selectedGitGroup = gitGroups.value.groups.find((gitGroup) => gitGroup.value.name === appSettings.value.gitGroup.name);\n    const currentRow = appDataTableRows.value.find((row) => row.appID == selectedRow.appID)!;\n    currentRow.microservicesDisable = true;\n    currentRow.microservices = [];\n    appSettings.value.microservice.name = \"\";\n    appSettings.value.microservice.ID = 0;\n\n    if (selectedGitGroup) {\n        appSettings.value.gitGroup.ID = selectedGitGroup.value.id;\n        appSettings.value.gitGroup.name = selectedGitGroup.value.name;\n\n        if (!selectedGitGroup.requestedBefore) {\n            debouncer.debounce(getMicroservicesByGroupID, 500, selectedGitGroup, currentRow);\n        } else {\n            currentRow.microservices = selectedGitGroup.microservicesCache;\n            currentRow.microservicesDisable = !currentRow.microservices.length;\n        }\n    } else {\n        appSettings.value.gitGroup.ID = 0;\n    }\n}\n\nasync function getMicroservicesByGroupID(selectedGitGroup: IGroup, currentRow: IModuleDataTableRow) {\n    currentRow.microservices = await appSettingsUseCase.getProjectsByGroupId(selectedGitGroup.value.id);\n    selectedGitGroup.microservicesCache = currentRow.microservices;\n    selectedGitGroup.requestedBefore = true;\n    currentRow.microservicesDisable = !currentRow.microservices.length;\n}\n\nasync function handleModulesGitGroupChange(selectedRow: IModuleDataTableRow) {\n    const selectedGitGroup = gitGroups.value.groups.find((gitGroup) => gitGroup.value.name === selectedRow.gitGroupName);\n    const currentRow = modulesDataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.gitGroupID = selectedGitGroup?.value?.id || 0;\n    currentRow.gitGroupName = selectedGitGroup?.value?.name || \"\";\n    if (!currentRow.gitGroupID) {\n        currentRow.microservices = [];\n        currentRow.microservicesDisable = true;\n        currentRow.microserviceName = \"\";\n        currentRow.microserviceID = 0;\n    }\n}\n\nasync function handleAppGitGroupChange(selectedRow: IAppDataTableRow) {\n    const selectedGitGroup = gitGroups.value.groups.find((gitGroup) => gitGroup.value.name === appSettings.value.gitGroup.name);\n    const currentRow = appDataTableRows.value.find((row) => row.appID == selectedRow.appID)!;\n    appSettings.value.gitGroup.ID = selectedGitGroup?.value?.id || 0;\n    appSettings.value.gitGroup.name = selectedGitGroup?.value?.name || \"\";\n    if (!selectedGitGroup) {\n        currentRow.microservices = [];\n        currentRow.microservicesDisable = true;\n        appSettings.value.microservice.name = \"\";\n        appSettings.value.microservice.ID = 0;\n    }\n}\n\nfunction handleModulesMicroServiceChange(selectedRow: IModuleDataTableRow) {\n    const selectedMicroservice = selectedRow.microservices.find((microservice) => microservice.name === selectedRow.microserviceName);\n    const currentRow = modulesDataTableRows.value.find((row) => row.moduleID == selectedRow.moduleID)!;\n    currentRow.microserviceName = selectedMicroservice?.name || \"\";\n    currentRow.microserviceID = selectedMicroservice?.id || 0;\n}\n\nfunction handleAppMicroServiceChange(selectedRow: IAppDataTableRow) {\n    const selectedMicroservice = selectedRow.microservices.find((microservice) => microservice.name === appSettings.value.microservice.name);\n    appSettings.value.microservice.name = selectedMicroservice?.name || \"\";\n    appSettings.value.microservice.ID = selectedMicroservice?.id || 0;\n}\n\nasync function onSave() {\n    if (currentApp.value) {\n        let settings = {};\n        const modulesSettings: Array<IModuleSettingsItem> = modulesDataTableRows.value\n            .filter(\n                (row) =>\n                    row.microserviceID !=\n                    (viewModel.studio.items.find((item) => item.ID == row.moduleID) as IModule).deployParameters?.gitServiceRepoId\n            )\n            .map((row) => ({\n                moduleID: row.moduleID,\n                deployParameters: {\n                    gitGroupId: row.gitGroupID,\n                    gitServiceRepoId: row.microserviceID,\n                },\n            }));\n\n        const applicationSettings: IApplicationSettingsItem = {\n            applicationID: currentApp.value!.ID,\n            deployParameters: {\n                gitProductRepoId: appSettings.value.product.ID,\n                gitGroupId: appSettings.value.gitGroup.ID,\n                gitServiceRepoId: appSettings.value.microservice.ID,\n            },\n        };\n\n        settings = { modulesSettings };\n\n        if (\n            currentApp.value.deployParameters?.gitProductRepoId != appSettings.value.product.ID ||\n            currentApp.value.deployParameters?.gitServiceRepoId != appSettings.value.microservice.ID\n        ) {\n            settings = { ...settings, applicationSettings };\n        }\n\n        await executor.execute(\n            async () => {\n                await appSettingsUseCase.update(settings);\n            },\n            { loading: true }\n        );\n        emit(\"close\");\n    }\n}\n</script>\n\n<template>\n<div class=\"deploy-setting-container\">\n    <h2>{{ localization.get(\"deploySettings_appSettings\") }}</h2>\n    <label for=\"product-input\">{{ localization.get(\"product\") }}</label>\n    <input\n        class=\"form-control\"\n        list=\"products\"\n        id=\"product-input\"\n        placeholder=\"Select Product\"\n        v-model=\"appSettings.product.name\"\n        @change=\"handleProductSelect\"\n        :disabled=\"!gitProducts.loaded\"\n    />\n    <datalist id=\"products\">\n        <option value=\"\" disabled selected>Select Product for Application</option>\n        <option v-for=\"product in gitProducts.products\">\n            {{ product.name }}\n        </option>\n    </datalist>\n    <v-client-table ref=\"table\" :data=\"appDataTableRows\" :columns=\"appAppDatatableColumns\" :options=\"appOptionsDataTable\">\n        <template #appName=\"props\">\n            <span>{{ props.row.appName }}</span>\n        </template>\n        <template #gitGroup=\"props\"\n            ><input\n                class=\"form-control\"\n                list=\"app-git-groups\"\n                id=\"app-git-group-input\"\n                placeholder=\"Select Git Group\"\n                v-model=\"appSettings.gitGroup.name\"\n                @input=\"handleAppGitGroupInput(props.row)\"\n                @change=\"handleAppGitGroupChange(props.row)\"\n                :disabled=\"!gitGroups.loaded\" />\n            <datalist id=\"app-git-groups\">\n                <option v-for=\"gitGroup in gitGroups.groups\" :value=\"gitGroup.value.name\"></option></datalist\n        ></template>\n        <template #microservice=\"props\"\n            ><input\n                class=\"form-control\"\n                :list=\"props.row.appID\"\n                id=\"app-microservice-input\"\n                placeholder=\"Select Microservice\"\n                v-model=\"appSettings.microservice.name\"\n                @change=\"handleAppMicroServiceChange(props.row)\"\n                :disabled=\"props.row.microservicesDisable\" />\n            <datalist :id=\"props.row.appID\">\n                <option v-for=\"microservice in props.row.microservices\" :value=\"microservice.name\"></option></datalist\n        ></template>\n    </v-client-table>\n    <h2>{{ localization.get(\"deploySettings_moduleSettings\") }}</h2>\n    <v-client-table ref=\"table\" :data=\"modulesDataTableRows\" :columns=\"modulesAppDatatableColumns\" :options=\"modulesOptionsDataTable\">\n        <template #moduleName=\"props\">\n            <span>{{ props.row.moduleName }}</span>\n        </template>\n        <template #gitGroup=\"props\">\n            <input\n                class=\"form-control\"\n                list=\"git-groups\"\n                id=\"git-group-input\"\n                placeholder=\"Select Git Group\"\n                v-model=\"props.row.gitGroupName\"\n                @input=\"handleModulesGitGroupInput(props.row)\"\n                @change=\"handleModulesGitGroupChange(props.row)\"\n                :disabled=\"!gitGroups.loaded\"\n            />\n            <datalist id=\"git-groups\">\n                <option v-for=\"gitGroup in gitGroups.groups\" :value=\"gitGroup.value.name\"></option>\n            </datalist>\n        </template>\n        <template #microservice=\"props\">\n            <input\n                class=\"form-control\"\n                :list=\"props.row.moduleID\"\n                id=\"microservice-input\"\n                placeholder=\"Select Microservice\"\n                v-model=\"props.row.microserviceName\"\n                @change=\"handleModulesMicroServiceChange(props.row)\"\n                :disabled=\"props.row.microservicesDisable\"\n            />\n            <datalist :id=\"props.row.moduleID\">\n                <option v-for=\"microservice in props.row.microservices\" :value=\"microservice.name\"></option>\n            </datalist>\n        </template>\n    </v-client-table>\n    <hr />\n    <div id=\"button-container\">\n        <span type=\"button\" id=\"previous-button\" data-v-33a93b5b=\"\" @click=\"() => emit('close')\">\n            {{ localization.get(\"cancel\") }}\n        </span>\n        <button class=\"default-button\" @click=\"onSave\">{{ localization.get(\"save\") }}</button>\n    </div>\n</div>\n</template>\n\n<style scoped>\n.deploy-setting-container {\n    padding: 5px;\n}\n#product-input {\n    width: 50%;\n}\n#button-container {\n    text-align: end;\n    vertical-align: bottom;\n}\n#previous-button {\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #2f3160;\n    opacity: 1;\n    font-weight: bold;\n}\n.default-button {\n    background: #2f3160 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    opacity: 1;\n    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-medium) var(--unnamed-font-size-20) / var(--unnamed-line-spacing-26)\n        var(--unnamed-font-family-roboto);\n    letter-spacing: var(--unnamed-character-spacing-0);\n    color: var(--white);\n    font: normal normal medium 20px/26px Roboto;\n    letter-spacing: 0px;\n    color: #ffffff;\n    opacity: 1;\n    margin-left: 10px;\n}\n</style>\n"],"sourceRoot":""}]);
